import { AttackIntent } from "../components/intent/AttackIntent";
import { MoveIntent } from "../components/intent/MoveIntent";
import { Position } from "../components/Position";
import { EnemyTag } from "../components/Tag/EnemyTag";
import { EntityManager } from "../core/EntityManager";
import { eventBus } from "../core/EventBus";
import { findPathInTemplate } from "../core/Pathfinding";
import { RoomTemplate } from "../data/types";

export class MovementSystem {
    constructor(
        private em: EntityManager,
        private currentRoom: RoomTemplate[]
    ) { }

    private RoomNumber: number = 0;

    private dirs = [
        { x:  1, y:  0 },
        { x: -1, y:  0 },
        { x:  0, y:  1 },
        { x:  0, y: -1 },
    ];

    public update(dt: number) {
        /**/console.log('[MovementSystem] update, dt=', dt);
        const movers = this.em.getEntityWithComponent(MoveIntent);
        for (const actorId of movers) {
            const mi = this.em.getComponent(actorId, MoveIntent)!;
            const { x, y } = mi.target;
            const pos = this.em.getComponent(actorId, Position)!;
            pos.x = x;
            pos.y = y;

            const path = findPathInTemplate(this.currentRoom[this.RoomNumber], pos, mi.target);
            /**/console.log('[MovementSystem] path for', actorId, '=', path);
            /**/console.log('[MovementSystem] processing MoveIntent for', actorId, 'goal=', mi.target);
            this.em.removeComponent(actorId, MoveIntent);

            if (path && path.length) {
                /***/console.log('[MovementSystem] emit movePath for', actorId);
                eventBus.emit("movePath", { entity: actorId, path });
            }

            const occupants = this.findEntitiesAtPosition(x, y);

            // если есть враг — планируем атаку
            const enemy = occupants.find(id => this.em.hasComponent(id, EnemyTag));
            if (enemy != null) {
                this.em.addComponent(actorId, new AttackIntent(enemy), AttackIntent);
                continue;
            }

            // если есть ловушка
            /*const trap = occupants.find(id => this.entityManager.hasComponent(id, TrapComponent));
            if (trap != null) {
                this.entityManager.addComponent(actor, new TrapIntent(trap), TrapIntent);
                continue;
            }*/

            // …другие проверки: двери → TransitionIntent и т.п.
        }
    }

    private findEntitiesAtPosition(x: number, y: number): number[] {
        const result: number[] = [];
        for (const id of this.em.getEntityWithComponent(Position)) {
            const p = this.em.getComponent(id, Position)!;
            if (p.x === x && p.y === y) result.push(id);
        }
        return result;
    }
}
